.film-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

}

.film {
  height: 270px;
  width: 280px;
  margin: 10px;
  border: solid var(--dark-night);
}

.film-img-wrapper {
  display: flex;
  background-color: white;
  height: 75%;
  position: relative;
}

.img-resp {
  /*https://stackoverflow.com/questions/11757537/css-image-size-how-to-fill-but-not-stretch*/
  object-fit: cover;
  width: 100%
}

.film-info {
  height: 25%;
}

.play {
  position: absolute;
  bottom: 0px;
  right: 0px;
  font-family: var(--bbc-font);
	background-color: black;
	font-weight: bold;
	color: var(--magenta);
}

.info {
  position: absolute;
  bottom: 0px;
  left: 0px;
  font-family: var(--bbc-font);
	background-color: #f3ce13;
	font-weight: bold;
	color: black;
}

.lnk__icon {
  -webkit-transition: fill .2s ease-out;
  transition: fill .2s ease-out;
  vertical-align: middle;
  width: 16px;
  height: 14px;
  flex-shrink: 0;
}

.grid-imdb {
  display: flex;
  justify-content: space-around;
}

.grid-score {
  font-weight: bold;
  color: var(--magenta);
}

.grid-votes {
  
}

.grid-title {
  font-size: 1.2em;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.grid-details {
  display: flex;
  justify-content: space-around;
}

