
.settingsContainer {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.containerItem {
  padding: 0.2em;
}

.containerItem span select {
  font-family: var(--bbc-font);
  background-color: #000;
  color: white;
}

.containerItem button {
  font-family: var(--bbc-font);
  background-color: #000;
  color: white;
}

@media 
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px) {
  .settingsContainer {
    flex-direction: column;
  }
  .containerItem {
    width: 100%;
    padding: 0.2em;
    text-align: center;
  }
}


