@import './gel-typography.css';

@font-face {
  font-family: 'ReithSans';
  src:  url('./fonts/BBCReithSans_W_Rg.woff2') format('woff2');
}

/* Core Palette - How iPlayer is expressed through colour */
:root {
  /* Core Background */
  /*
  iPlayer Pink is the primary brand colour and is used in most features. 
  For interface elements with white text on top, Magenta should be used to improve accessibility.
  */
  --magenta: #F54997; /* iPlayer Pink */
  --raspberry: #CF3E80; /* magenta */

  /*Bacground Color*/
  /*
  Midnight is the primary background colour 
  with Dark Night and Black being secondary colours
  */
  --midnight-blue: #0C101C; /* Midnight */
  --dark-night: #020011; /* Dark Night */
  --black: #000; /* Black */

  /*Typography*/
  /*
  White is used as the primary colour for type 
  and any secondary information should use Dusk.
  */
  --white: #FFF; /* White */
  --dusk: #B2B7C4; /* Dusk */

  /*Supporting Elements*/
  /*
  Nightfall is used on a number of interface elements when a subtle emphasis is needed. 
  It can be used to distinguish elements from the background, such as borders and buttons.
  */
  --nightfall: #242833; /* Nightfall */

  /*Messaging*/
  /*
  These colours should only be used within icons which appear in messaging dialogues, such as the guidance notice
  */
  --warning: #BB1919; /* Warning */
  --alert: #EBB100; /* Alert */
  --info: #126DA6; /* Info */

  --bbc-font: ReithSans, Arial, Helvetica, freesans, sans-serif;
}

body {
  font-family: var(--bbc-font);
  margin: 10px;
  background-color: var(--midnight-blue);
  color: var(--white);
}
