.pagination {
  display: flex;
  justify-content: space-around;
}

.paginationContainer {
  display: flex;
}

.paginationItem {
  padding: 0.2em;
}

.paginationItem button {
  font-family: var(--bbc-font);
  background-color: #000;
  color: white;
}

.paginationItem select {
  font-family: var(--bbc-font);
  background-color: #000;
  color: white;
}